#payment {
  .address {
    border: 1px solid #000;
    padding: 10px;
  }

  .note {
    border: 1px solid #000;
    padding: 10px;
  }

  p {
    font-family: "Poppins", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
    /* or 18px */

    color: #212121;
    margin: 0;
  }

  h5 {
    font-family: "Poppins", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 21px;
    /* identical to box height */

    color: #212121;
  }

  .btn-payment {
    margin-top: 105px;
  }

  .product-card {
    img {
      width: 150px;
    }

    .desc {
      width: 100%;
      margin: 0 10px;
      ul {
        padding-left: 20px;

        li {
          font-family: "Poppins", sans-serif;
          font-style: normal;
          font-weight: 700;
          font-size: 12px;
          line-height: 18px;
          /* identical to box height */

          color: #c4c4c4;
        }
      }
    }
  }

  .discount {
    border: 0.5px solid #c4c4c4;
    box-sizing: border-box;
    border-radius: 4px;
    padding: 22px 14px;
  }

  .billing-detail {
    background: #ffffff;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15);
    border-radius: 8px;

    padding: 30px 25px;
  }
}

