.table-report-brands {
  thead {
    tr {
      .parent-head {
        border: 1px solid #fff;
      }

      th:last-child {
        border-radius: 0;
      }
      th:first-child {
        border-radius: 0;
        border: none;
      }
      .leadshead {
        border-bottom: none;
        border-top: none;
      }
    }
  }
}
