.detail-activity {
  position: fixed;
  top: 0;
  right: -665px;
  background-color: #fff;
  max-width: 665px;
  height: 100%;
  border-radius: 50px 0px 0px 50px;
  padding: 30px 50px;
  overflow: auto;
  z-index: 100;

  h1 {
    font-style: normal;
    font-weight: 700;
    font-size: 36px;
    line-height: 54px;
    /* identical to box height */

    color: #313132;
  }

  h3 {
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 27px;
    /* identical to box height */

    color: #313132;
  }
}

.detail-activity p {
  font-size: 14px;
  margin-bottom: 5px;
}

// .activity-active {
//   -webkit-animation: slideOn 0.5s forwards;
//   animation: slideOn 0.5s forwards;
// }

@-webkit-keyframes slideOn {
  0% {
    right: -665px;
  }
  100% {
    right: 0;
  }
}

@keyframes slideOn {
  0% {
    right: -665px;
  }
  100% {
    right: 0;
  }
}

.detail-activity-header img {
  width: 24px;
  height: 24px;
  cursor: pointer;
}

.activity-detail-btn {
  cursor: pointer;
}
