.popup {
  border-radius: 8px;
  width: 100%;
  position: fixed;
  top: 0;
  right: 0;
  border: 3px solid #f1f1f3;
  z-index: 103;
  height: 100%;
  display: none;
  background-color: rgba(0, 0, 0, 0.5);
  overflow: auto;
}

.popup-layout {
  width: 100%;
  height: 100vh;
  border-radius: 8px;
  position: fixed;
  top: 0;
  right: 0;
  border: 3px solid #f1f1f3;
  z-index: 103;
  height: 100%;
  display: none;
  background-color: rgba(0, 0, 0, 0.5);
  overflow: auto;
}

.com-active {
  display: block;
}

.popup-container {
  height: fit-content;
  width: fit-content;
  margin: 20px auto;
}
