.approval-detail {
  width: 665px;
  position: fixed;
  height: 100vh;
  transition: all 0.5s ease;
  right: -665px;
  top: 0;
  z-index: 50;
  background-color: #fff;
  border-radius: 50px 0 0 50px;
  padding: 30px 50px;
  overflow: scroll;

  .header {
    h1 {
      color: #313132;
      font-size: 36px;
      font-style: normal;
      font-weight: 700;
      line-height: 54px;
    }

    img {
      cursor: pointer;
      height: 24px;
      width: 24px;
    }
  }

  p{
    margin-bottom: 0.5rem;
  }
}

.activity-active {
  transition: all 0.5s ease;
  right: 0 !important;
}
